import React, {
    ChangeEvent,
    FormEvent,
    memo,
    useEffect,
    useState,
    VFC,
} from "react";
import { Oval } from "react-loader-spinner";
import { toast } from "react-toastify";
import axios from "../../libs/axios";
import { useUpdateProfile, useUser } from "../../queries/AuthQuery";

import { Alert } from "../atoms/inputForm/Alert";
import { Button } from "../atoms/inputForm/Button";
import { Input } from "../atoms/inputForm/Input";
import { InputPic } from "../atoms/inputForm/InputPic";
import { InputTextArea } from "../atoms/inputForm/InputTextArea";
import { Label } from "../atoms/inputForm/Label";
import { Notes } from "../atoms/inputForm/Notes";
import { Title } from "../atoms/inputForm/Title";
import { Spinner } from "../atoms/spinner/Spinner";
import { UserComponent } from "../molecules/inputForm/UserComponent";
import { Form } from "../organisms/inputForm/Form";

// 
type Form = {
    name: string;
    email: string;
    profile: string;
    pic: File | string;
    error_list: {
        name: string;
        email: string;
        profile: string;
        pic: string;
    };
};

// 
export const EdifProfile: VFC = memo(() => {
    // 
    const [formData, setFormData] = useState<Form>({
        name: "",
        email: "",
        profile: "",
        pic: "",
        error_list: {
            name: "",
            email: "",
            profile: "",
            pic: "",
        },
    });

    // DB
    const [dbPic, setDbPic] = useState("");

    // 
    const { data, isLoading } = useUser();

    // 
    const updateProfile = useUpdateProfile();

    // 
    useEffect(() => {
        if (data) {
            setFormData({
                ...formData,
                // null
                name: data.name ?? "",
                email: data.email ?? "",
                profile: data.profile ?? "",
            });

            setDbPic(data.pic ?? "");
        }
    }, [data]);

    // 
    const handleChange = (
        e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
    ) => {
        const { name, value } = e.target;

        setFormData({ ...formData, [name]: value });
    };

    // 
    const handlePicChange = (e: ChangeEvent<HTMLInputElement>) => {
        if (e.target.files === null) {
            return;
        }
        const file = e.target.files[0];
        if (file === null) {
            return;
        }

        setFormData({
            ...formData,
            pic: e.target.files[0],
        });
    };

    // 
    const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
        // 
        e.preventDefault();

        // 
        const data = {
            name: formData.name,
            email: formData.email,
            profile: formData.profile,
            pic: formData.pic,
        };

        // API
        axios.get("/sanctum/csrf-cookie").then((res) => {
            updateProfile.mutate(data, {
                onError: (err: any) => {
                    if (err.response.status === 422) {
                        const newFormData = {
                            ...formData,
                            error_list: err.response.data.errors,
                        };

                        setFormData(newFormData);
                    } else {
                        toast.error(
                            "",
                            {
                                position: toast.POSITION.TOP_CENTER,
                                autoClose: 3000,
                            }
                        );
                    }
                },
            });
        });
    };

    return (
        <>
            {isLoading ? (
                <Spinner>
                    <Oval
                        height={80}
                        width={80}
                        color="#555"
                        visible={true}
                        ariaLabel="oval-loading"
                        secondaryColor="#555"
                        strokeWidth={2}
                        strokeWidthSecondary={2}
                    />
                </Spinner>
            ) : (
                <Form onSubmit={handleSubmit}>
                    <Title></Title>

                    <UserComponent>
                        <Label>
                            
                            <Notes>&#8251;</Notes>
                            <Input
                                type="text"
                                name="name"
                                placeholder=""
                                value={formData.name}
                                onChange={handleChange}
                                isValid={formData.error_list.name}
                                autoFocus
                                autoComplete="username"
                            />
                        </Label>
                        <Alert>{formData.error_list.name}</Alert>
                    </UserComponent>

                    <UserComponent>
                        <Label>
                            
                            <Notes>&#8251;</Notes>
                            <Input
                                type="text"
                                name="email"
                                placeholder=""
                                value={formData.email}
                                onChange={handleChange}
                                isValid={formData.error_list.email}
                                autoComplete="email"
                            />
                        </Label>
                        <Alert>{formData.error_list.email}</Alert>
                    </UserComponent>

                    <UserComponent>
                        <Label>
                            
                            <Notes>&#8251;</Notes>
                            <InputTextArea
                                value={formData.profile}
                                name="profile"
                                isValid={formData.error_list.profile}
                                onChange={handleChange}
                            />
                        </Label>
                        <Alert>{formData.error_list.profile}</Alert>
                    </UserComponent>

                    <UserComponent>
                        <Label>
                            
                            <Notes>&#8251;</Notes>
                            <InputPic
                                onChange={handlePicChange}
                                dbPic={dbPic}
                                role="user"
                            />
                        </Label>
                        <Alert>{formData.error_list.pic}</Alert>
                    </UserComponent>

                    <Button
                        value=""
                        isLoading={updateProfile.isLoading}
                    />
                </Form>
            )}
        </>
    );
});
