import React, { memo, useCallback, useEffect, useState, VFC } from "react";
import { Oval } from "react-loader-spinner";
import axios from "../../../libs/axios";
import { useGetRegistItems } from "../../../queries/ItemsQuery";
import {
    Category,
    ChildItem,
    Paginate,
    ParentItem,
    User,
} from "../../../types/api/item";

import { MypageTitle } from "../../atoms/item/MypageTitle";
import { Spinner } from "../../atoms/spinner/Spinner";
import { Empty } from "../../molecules/mypage/Empty";
import { Index } from "../mypage/Index";
import { Pagination } from "./Pagination";
import { Body } from "./panel/Body";
import { PanelMaster } from "./panel/PanelMaster";
import { RegistPanel } from "./panel/RegistPanel";

// 
type IParentItem = ParentItem & {
    category: Category;
    child_items: ChildItem[];
    user: Omit<User, "profile">;
};

// 
type RegistItem = Paginate & {
    data: IParentItem[];
};

// MyMove
export const RegistList: VFC = memo(() => {
    // MyMove
    const [RegistItems, setRegistItems] = useState<RegistItem>({
        current_page: 1,
        data: [
            {
                category: {
                    id: 0,
                    name: "",
                },
                category_id: 0,
                child_items: [
                    {
                        id: 0,
                        name: "",
                        parent_item_id: 0,
                    },
                ],
                cleartime: "",
                created_at: "",
                id: 0,
                name: "",
                pic: "",
                user_id: 0,
                user: {
                    id: 0,
                    name: "",
                    pic: "",
                },
            },
        ],
        first_page_url: "",
        from: 0,
        last_page: 0,
        last_page_url: "",
        links: [
            {
                active: false,
                labe: "",
                url: "",
            },
        ],
        next_page_url: "",
        path: "",
        per_page: 0,
        prev_page_url: 0,
        to: 0,
        total: 0,
    });

    // 
    const [currentPage, setCurrentPage] = useState(1);

    // MyMove
    const { data, isLoading } = useGetRegistItems(currentPage);

    // MyMovestate
    const getRegistItems = useCallback(() => {
        if (data) {
            setRegistItems(data);
        }
    }, [data]);

    // 
    const deleteItem = (id: number) => {
        console.log(id);

        if (
            !window.confirm(
                "?"
            )
        ) {
            console.log("");
            return false;
        } else {
            console.log("");

            axios
                .post(`/items/${id}/delete`)
                .then((res) => {
                    console.log(res);
                    getRegistItems();
                })
                .catch((err) => {
                    console.log(err);
                });
            return true;
        }
    };

    // 
    const movePage = (page: number) => {
        setCurrentPage(page);
    };

    // MyMove
    useEffect(() => {
        if (data) {
            getRegistItems();
        }
    }, [data]);
    return (
        <Index>
            <MypageTitle>MyMove</MypageTitle>

            {isLoading ? (
                <Spinner>
                    <Oval
                        height={80}
                        width={80}
                        color="#555"
                        visible={true}
                        ariaLabel="oval-loading"
                        secondaryColor="#555"
                        strokeWidth={2}
                        strokeWidthSecondary={2}
                    />
                </Spinner>
            ) : RegistItems?.data.length === 0 ||
              RegistItems?.data[0].id === 0 ? (
                <Empty>
                    <p>MyMove</p>
                </Empty>
            ) : (
                <PanelMaster>
                    <Body>
                        {RegistItems?.data.map((el, i) => (
                            <RegistPanel
                                key={el.id}
                                itemId={el.id}
                                itemLength={el.child_items.length}
                                itemPic={el.pic}
                                categoryName={el.category.name}
                                itemName={el.name}
                                userPic={el.user.pic}
                                userName={el.user.name}
                                itemDate={el.created_at}
                                itemClearTime={el.cleartime}
                                deleteItem={deleteItem}
                            />
                        ))}
                    </Body>

                    <Pagination
                        prev_page_url={RegistItems.prev_page_url}
                        next_page_url={RegistItems.next_page_url}
                        current_page={RegistItems.current_page}
                        last_page={RegistItems.last_page}
                        movePage={movePage}
                    />
                </PanelMaster>
            )}
        </Index>
    );
});
